___CSS_LOADER_EXPORT___.push([module.id, `* {
  --bg-color: whitesmoke;
  --font-color: black;
  --input-color: white;
  --big-border-radius: 10px;
  --small-border-radius: 5px;
  --small-padding: 10px;
  --todo-card-bg: white;
  --shadow: hsl(0deg 0 50%);
  --low-prio-bg: hsl(120, 87%, 90%);
  --med-prio-bg: hsl(60, 88%, 90%);
  --hig-prio-bg: hsl(0, 87%, 90%);
  box-sizing: content-box;
  font-family: Arial, Ubuntu, sans-serif;
  color: var(--font-color);
}

@media (prefers-color-scheme: dark) {
  * {
    --bg-color: #3f4f4f;
    --font-color: whitesmoke;
    --input-color: dimgray;
    --todo-card-bg: black;
    --low-prio-bg: hsl(120, 25%, 50%);
    --med-prio-bg: hsl(60, 25%, 50%);
    --hig-prio-bg: hsl(0, 25%, 50%);
  }
}

.blank-todo {
  width: clamp(100px, 90%, 800px);
}

body {
  background-color: var(--bg-color);
  margin: 0;
}

html {
  padding: 0;
}

#header {
  border-bottom: 1px solid var(--font-color);
  background: var(--todo-card-bg);
}

h1 {
  margin-top: 0;
  text-align: center;
  padding-top: 16px;
}

.project-container {
  background-color: hsla(56, 88%, 90%, 0.5);
  border-radius: var(--big-border-radius);
  display: flex;
  justify-content: space-evenly;
  padding: var(--small-padding);
}

.project-button {
  padding: var(--small-padding);
  border-radius: var(--small-border-radius);
  font-size: 1.2em;
}

.todo {
  border: 1px solid var(--font-color);
  border-radius: var(--big-border-radius);
  padding: var(--small-padding);
  background-color: var(--todo-card-bg);
}

.red-bg {
  background-color: hsl(0 100 60);
}

.blue-bg {
  background-color: DodgerBlue;
}

.todo:focus-within, .todo:hover {
  box-shadow: 0 0 5px 5px var(--shadow);
}

.checked {
  text-decoration: line-through;
}

.low {
  background-color: var(--low-prio-bg);
}

.medium {
  background-color: var(--med-prio-bg);
}

.high {
  background-color: var(--hig-prio-bg);
}

textarea {
  background-color: transparent;
  border-radius: var(--small-border-radius);
  padding: var(--small-border-radius);
  font-size: 1em;
  width: 80%;
}

input {
  background-color: transparent;
  border-radius: var(--small-border-radius);
  padding: var(--small-border-radius);
  font-size: 1em;
}

button {
  background-color: var(--input-color);
  border-radius: var(--small-border-radius);
  padding: var(--small-border-radius);
}

select {
  font-size: 1.0em;
  background-color: transparent;
  border-radius: var(--small-border-radius);
  border: none;
}

option {
  background-color: var(--bg-color);
}

.with-margins {
  margin: 20px;
}

#created-todos {
  display: flex;
  flex-flow: row-reverse wrap-reverse;
  gap: 12px;
  justify-content: space-evenly;
}

.hidden {
  display: none;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;EACtB,mBAAmB;EACnB,oBAAoB;EACpB,yBAAyB;EACzB,0BAA0B;EAC1B,qBAAqB;EACrB,qBAAqB;EACrB,yBAAyB;EACzB,iCAAiC;EACjC,gCAAgC;EAChC,+BAA+B;EAC/B,uBAAuB;EACvB,sCAAsC;EACtC,wBAAwB;AAC1B;;AAEA;EACE;IACE,mBAAmB;IACnB,wBAAwB;IACxB,sBAAsB;IACtB,qBAAqB;IACrB,iCAAiC;IACjC,gCAAgC;IAChC,+BAA+B;EACjC;AACF;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,iCAAiC;EACjC,SAAS;AACX;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,0CAA0C;EAC1C,+BAA+B;AACjC;;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,yCAAyC;EACzC,uCAAuC;EACvC,aAAa;EACb,6BAA6B;EAC7B,6BAA6B;AAC/B;;AAEA;EACE,6BAA6B;EAC7B,yCAAyC;EACzC,gBAAgB;AAClB;;AAEA;EACE,mCAAmC;EACnC,uCAAuC;EACvC,6BAA6B;EAC7B,qCAAqC;AACvC;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,qCAAqC;AACvC;;AAEA;EACE,6BAA6B;AAC/B;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,6BAA6B;EAC7B,yCAAyC;EACzC,mCAAmC;EACnC,cAAc;EACd,UAAU;AACZ;;AAEA;EACE,6BAA6B;EAC7B,yCAAyC;EACzC,mCAAmC;EACnC,cAAc;AAChB;;AAEA;EACE,oCAAoC;EACpC,yCAAyC;EACzC,mCAAmC;AACrC;;AAEA;EACE,gBAAgB;EAChB,6BAA6B;EAC7B,yCAAyC;EACzC,YAAY;AACd;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,mCAAmC;EACnC,SAAS;EACT,6BAA6B;AAC/B;;AAEA;EACE,aAAa;AACf","sourcesContent":["* {\n  --bg-color: whitesmoke;\n  --font-color: black;\n  --input-color: white;\n  --big-border-radius: 10px;\n  --small-border-radius: 5px;\n  --small-padding: 10px;\n  --todo-card-bg: white;\n  --shadow: hsl(0deg 0 50%);\n  --low-prio-bg: hsl(120, 87%, 90%);\n  --med-prio-bg: hsl(60, 88%, 90%);\n  --hig-prio-bg: hsl(0, 87%, 90%);\n  box-sizing: content-box;\n  font-family: Arial, Ubuntu, sans-serif;\n  color: var(--font-color);\n}\n\n@media (prefers-color-scheme: dark) {\n  * {\n    --bg-color: #3f4f4f;\n    --font-color: whitesmoke;\n    --input-color: dimgray;\n    --todo-card-bg: black;\n    --low-prio-bg: hsl(120, 25%, 50%);\n    --med-prio-bg: hsl(60, 25%, 50%);\n    --hig-prio-bg: hsl(0, 25%, 50